.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  -webkit-transition: visibility .3s ease-in-out, opacity .5s linear, height .5s ease-in-out;
  -moz-transition: visibility .3s ease-in-out, opacity .5s linear, height .5s ease-in-out;
  -ms-transition: visibility .3s ease-in-out, opacity .5s linear, height .5s ease-in-out;
  -o-transition: visibility .3s ease-in-out, opacity .5s linear, height .5s ease-in-out;
  transition: visibility .3s ease-in-out, opacity .5s linear, height .5s ease-in-out; }
  @media (min-width: 768px) {
    .navegacion {
      display: flex;
      align-items: center;
      opacity: 1;
      visibility: visible; } }
  .navegacion a {
    color: #FFFFFF;
    text-decoration: none;
    font-size: 2.2rem;
    display: block;
    text-align: center; }
    .navegacion a::after {
      content: '';
      display: block;
      width: 0;
      height: .3rem;
      background-color: #FFFFFF;
      margin-top: 1rem;
      -webkit-transition: width .3s ease-in-out;
      -moz-transition: width .3s ease-in-out;
      -ms-transition: width .3s ease-in-out;
      -o-transition: width .3s ease-in-out;
      transition: width .3s ease-in-out; }
    .navegacion a:hover::after {
      width: 100%;
      -webkit-transition: width .3s ease-in-out;
      -moz-transition: width .3s ease-in-out;
      -ms-transition: width .3s ease-in-out;
      -o-transition: width .3s ease-in-out;
      transition: width .3s ease-in-out; }
    @media (min-width: 768px) {
