.no-webp .inicio {
    background-image: url(../img/header.jpg);
}

.webp .inicio {
    background-image: url(../img/header.webp);
}

.header {
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio {
        
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        height: 100vh;
       
        @include tablet {
            min-height: 70rem;
        } 

        .contenido-header {
            @include tablet {
                min-height: 70rem;
            } 
        }
    }
}

.contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    h1 {
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 2;
        font-size: 2.7rem;
        max-width: 50rem;

        @include tablet {
            font-size: 3.8rem;
            max-width: 60rem;
        }
    }
}
.contenido-crf{
    height: 85vh;
}